.card {
    width: 100px;
    height: 150px;
    border: 2px solid #000;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: bold;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
  }
  
  .card:hover {
    transform: scale(1.05);
  }
  
  .card.flipped {
    background-color: #f0f0f0;
  }
  
  .card p {
    margin: 0;
  }
  
  .card.red-text {
    color: red;
  }
  
  .card.black-text {
    color: black;
  }
  